// >> this file is for the variables that gonna be used in the project:

@use "functions" as *;

// >> how to use extend:
%error {
  color: #e2494c;
  font-size: medium;
}
// .element {
//   @extend %error;
//   font-weight: bold;
// }

// >> The line height for the whole project:

$line-height: 1.6;

// >> The transition for the whole project:

$transition: 0.5s; 

// >> The scale for the whole project:

$scale: (1.2);

// >> The border-radius for the whole project:

$border-radius: 5px;

// >> The section-padding for the whole project:

$section-padding-md: rem(50);
$section-padding: rem(100);

$section-margin-md: rem(50);
$section-margin: rem(100);

// >> The section-margin for the whole project:

$txt-padding-sm: rem(10);
$txt-padding-md: rem(20);
$txt-padding-lg: rem(50);
$txt-padding-xlg: rem(100);

// >> The text margin for the whole project:

$txt-margin-sm: rem(10);
$txt-margin-md: rem(20);
$txt-margin-lg: rem(50);
$txt-margin-xlg: rem(100);

// >> center the element:

%text-center {
  text-align: center;
}

// >> hide the element:

%hidden {
  display: hidden;
}

// >> float the element left to right:

%float_left {
  float: left;
  direction: ltr;
}

// >> float the element right to left:

%float_right {
  float: right;
  direction: rtl;
}

// >> clearfix the element after floating:
%clearfix {
  clear: both;
}
