// Here you can create and customize the pages' header (Navigation):

@use "../globals" as *;
@use "../helpers" as *;
@use "../mixins" as *;

%header {
  position: absolute;
  font-family: 'Oswald', sans-serif;
  .toggle-btn {
    position: absolute;
      top: 3.125rem;
      cursor: pointer;
      @include breakpoint(laptop) {
        display: none;
      }
      span {
        display: block;
        height: .125rem;
        width: 1.375rem;
        transition: $transition;
        background-color: var(--white-clr);
        &:not(:last-child) {
          margin-bottom: .3125rem;
        }
      }
      &:hover {
        & span:first-child {
          transform: translateY(-.2rem);
        }
        & span:last-child {
          transform: translateY(.2rem);
        }
      }
  }
  .aside-menu {
    position: fixed;
    top: 0;
    left: -100vw;
    z-index: 10;
    min-height: 100vh;
    transition: $transition;
    width: calc(100vw - 1.5rem);
    max-width: 400px;
    background-image: url("../images/bg-mobile.jpg");
    background-size: cover;
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: var(--transparent-white-clr);
    }
    @include breakpoint(laptop) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      position: static;
      width: 100% !important;
      max-width: 100%;
      min-height: 5.625rem;
      left: 0;
      background-image: none;
      &::after {
        display: none;
      }
    }
    .head {
      position: relative;
      z-index: 2;
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-bottom: 1px solid var(--transparent-black-clr);
      padding: $txt-margin-sm $txt-padding-md;
      .close-btn {
        position: relative;
        width: 2rem;
        height: 2rem;
        cursor: pointer;
        &:hover {
          &::after,
          &::before {
            background-color: var(--red-clr);
          } 
        }
        &:before, 
        &:after {
          content: "";
          position: absolute;
          top: 50%;
          width: 100%;
          height: 2px;
          transition: $transition;
          transform-origin: center center;
          background-color: var(--black-clr);
        }
        &:before {
          transform: rotate(45deg);
        }
        &:after {
          transform: rotate(-45deg);
        }
      }
      @include breakpoint(laptop) {
        display: none;
      }
    }
    .navbar {
      position: relative;
      z-index: 2;
      @include breakpoint(laptop) {
        display: flex;
        align-items: center;
        justify-content: space-around;
      }
      .nav-item {
        display: block;
        .nav-link {
          display: block;
          width: 100%;
          cursor: pointer;
          letter-spacing: 1px;
          padding: $txt-padding-md;
          transition: color $transition;
          color: var(--black-clr);
          &:hover {
            background-color: var(--white-clr);
          }
          @include breakpoint(laptop) {
            color: var(--white-clr);
            padding-bottom: $txt-padding-sm;
            
            &:hover {
              background-color: transparent;
            }
          }
        }
        .mega-menu {
          display: flex;
          align-items: center;
          justify-content: space-between;
          svg {
            font-size: 10px;
            margin-left: 5px;
            transition: $transition;
          }
        }
      }
      .sub-navbar {
        display: none;
        transition: $transition;
        &.active {
          display: block;
        }
        .sub-nav-item {
          display: block;
          .sub-nav-link {
            display: block;
            width: 100%;
            letter-spacing: 1px;
            padding: $txt-padding-md $txt-padding-lg;
            transition: color $transition;
            border-left: 3px solid transparent;
            color: var(--black-clr);
            &:hover {
              border-left-color: var(--primary-clr);
              color: var(--primary-clr);
            }
            @include breakpoint(laptop) {
              font-size: .75rem;
              background-color: var(--white-clr);
              padding: $txt-padding-sm;
            }
          }
        }
        @include breakpoint(laptop) {
          position: absolute;
          width: 270px;
          padding: $txt-padding-sm $txt-padding-lg $txt-padding-sm $txt-padding-sm;
          &::after {
            content: "";
            position: absolute;
            top: -10px;
            left: 20px;
            width: 0;
            height: 0;
            border: 10px solid transparent;
            border-bottom-color: var(--white-clr);
          }
        }
      }
    }
  }
  .phone {
    position: relative;
    font-size: 1.125rem;
    padding: $txt-margin-sm $txt-margin-md;
    z-index: 2;
    svg {
      padding-right: $txt-margin-sm;
    }
    @include breakpoint(laptop) {
      color: var(--white-clr);
      padding-right: 0;
    }
  }
  .whatsapp {
    display: block;
    position: relative;
    font-weight: bold;
    transition: $transition;
    z-index: 2;
    color: var(--green-clr);
    padding: $txt-margin-sm $txt-margin-md;
    .wa {
      padding-right: $txt-margin-sm;
    }
    @include breakpoint(laptop) {
      position: absolute;
      bottom: -33rem;
      right: .5rem;
      font-weight: normal;
      border-radius: $border-radius;
      color: var(--white-clr);
      background-color: var(--green-clr);
      &:hover {
        background-color: var(--primary-clr);
      }
    }
  }
}

