// >> this file is for the typography rules that gonna be used in the project:

@use "../helpers" as *;
@use "fonts" as *;

/* ----------- Start Typography Rules ------------*/

body {
  line-height: 1.5;
  font-family: $body-font;
  min-height: 250rem;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: rem(24);
  margin-bottom: rem(3);
  @include breakpoint(desktop) {
    font-size: rem(28);
  }
}

h2 {
  font-size: rem(24);
  margin-bottom: rem(24);
}

a,
a:visited,
a:active {
  text-decoration: none;
}
/* ---------- End Typography Rules -------------*/

