// 4 - >> typography:
// 5 - >> manipulation:
// 6 - >> misc:

// start header //
header {
  @extend %header;
}
/* End header */
/* Start Landing Section */
.landing {
  display: block;
  height: 380px;
  background-image: url("../images/mobilanabg.png");
  background-size: cover;
  background-position: top center;
  @include breakpoint(laptop) {
    background-image: url("../images/landing.png");
  }
}

/* End Landing Section */

/* End Contact Section */

.contact {
  padding-top: $section-padding-md ;
  padding-bottom: $section-padding-md ;
  text-align: center;
  h2 {
    position: relative;
    color: var(--purple-clr);
    @extend %section_header-4;
  }
  p {
    font-size: .875rem;
    padding-top: $txt-padding-sm;
    color: var(--text-clr);
    span {
      display: none;
    }
  }
  .social-icons {
    margin-top: $txt-margin-md;
    img {
      width: 3rem;
      height: 3rem;
      margin-right: .5rem;
      transition: $transition;
      &:hover {
        transform: translateY(-.5rem);
        filter: saturate(1.5);
      }
    }
  }
  @include breakpoint(tablet) {
    p {
      font-size: 1rem;
      span {
        display: inline-block;
      }
    }
    .social-icons {
      img {
        width: 4rem;
        height: 4em;
      }
    }
  }
}

/* End Contact Section */

