// This is a small lib For headings (h1, h2, h3):

@use "../globals" as *;
@use "../helpers" as *;

// 1- >> section header:
%section_header-1 {
  text-align: center;
  h2 {
    position: relative;
    font-weight: normal;
    font-size: rem(40);
    margin-bottom: $txt-margin-md;
    text-transform: uppercase;
    &::before {
      content: "";
      position: absolute;
      width: rem(120);
      height: rem(2);
      bottom: rem(-30);
      left: 50%;
      transform: translateX(-50%);
      background-color: #333;
    }
    &::after {
      content: "";
      position: absolute;
      bottom: rem(-38);
      height: rem(14);
      width: rem(14);
      left: 50%;
      transform: translateX(-50%);
      border-radius: 50%;
      background-color: #fff;
      border: 2px solid #333;
    }
  }
  p {
    line-height: $line-height;
    margin: 0 auto rem(75) auto;
    color: blueviolet;
    max-width: 100%;
    width: rem(550);
    font-size: rem(16);
  }
}

// 2- >> section header:

%section_header-2 {
  position: relative;
  z-index: 2;
  width: fit-content;
  font-size: 30px;
  text-align: center;
  margin: 0 auto 100px;
  padding: 10px 20px;
  transition: $transition;
  border: 2px solid black;
  text-transform: uppercase;
  &::before,
  &::after {
    content: "";
    z-index: -1;
    position: absolute;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    top: 50%;
    transform: translateY(-50%);
    transition: $transition;
    background-color: var(--primary-clr);
  }
  &::before {
    left: -30px;
  }
  &::after {
    right: -30px;
  }
  &:hover::before {
    animation: move-left 0.5s linear forwards;
  }
  &:hover::after {
    animation: move-right 0.5s linear forwards;
  }
  &:hover {
    border-color: #fff;
    color: white;
    transition-delay: 0.5s;
  }
}

// 3- >> section header:

%section_header-3 {
  .section-heading {
    color: #ebeced;
    font-size: 60px;
    text-align: center;
    letter-spacing: -3px;
    margin: 0;
  }
  > p:first-of-type {
    font-size: 18px;
    text-align: center;
    margin-top: -20px;
    color: #797979;
    max-width: 250px;
    margin: -20px auto 80px;
  }
  @include breakpoint(tablet) {
    .section-heading {
      font-size: 80px;
    }
    > p:first-of-type {
      font-size: 20px;
      max-width: unset;
    }
  }
}

// 4- >> section header:
%section_header-4 {
  &::before {
    content: "";
    position: absolute;
    bottom: -15px;
    left: 50%;
    transform: translateX(-50%);
    width: 50px;
    height: 5px;
    border-radius: $border-radius;
    background-color: var(--gray-clr);
  }
}


